.body,
#root {
  --color-primary: #f37e1f;
  --color-secundary: rgb(141, 31, 14);
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.HolyGrail-body {
  display: flex;
  flex: 1;
}
.HolyGrail-content {
  flex: 1;
}

.HolyGrail-nav,
.HolyGrail-ads {
  /* 12em is the width of the columns */
  flex: 0 0 12em;
}

.HolyGrail-nav {
  /* put the nav on the left */
  order: -1;
}
header {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  border-bottom: 1px solid #e7e5e5;
  margin: 20px;
}

footer {
  display: flex;
  justify-content: space-around;
  background-color: #e7e7e6;
  width: 100%;
  text-align: center;
  padding-top: 40px;
}
main,
nav,
aside {
  padding: 1rem;
}
footer {
  line-height: 5rem;
}

.content {
  display: flex;
  text-align: center;
  margin: 20px;
}
.imgPetit {
  width: 200px;
  height: 200px;
  border-radius: 250px;
  padding: 20px;
}
.imgGran {
  width: 500px;
  height: 500px;
  border-radius: 250px;
}
.imgnull {
  display: none;
}

/* css contenido */

.imgsection {
  display: flex;
  justify-content: space-evenly;
}
.item1 {
  position: relative;
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(0, 0, 0, 0.8);
  padding: 10px;
  text-align: center;
  border-radius: 150px;
  border: 1px solid rgb(175, 174, 174);
  margin: 10px;
}
.section1 {
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
}
.itemsection {
  width: 40%;
  margin: 40px;
  margin-right: 60px;
  padding: 20px;
  overflow: hidden;
  max-width: 1200px;
  text-align: center;
  border-radius: 50px;
}
.imgsection1 {
  border-radius: 50px;
}
.itemtxtlegal {
  align-items: center;
  text-align: left;
  width: 60%;
}

h1.img {
  color: #e7e7e6;
  font-size: 30px;
}
.ulmenu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: var(--color-primary);
}

.limenu {
  float: left;
}

.limenu a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
.ulfooter {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.lifooter a {
  display: block;
  color: rgb(94, 93, 93);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.imgcirculo {
  width: 300px;
  height: 300px;
  border-radius: 150px;
}

.footercontent {
  display: flex;
  justify-content: space-around;
  background-color: #e7e7e6;
  width: 100%;
  text-align: center;
  padding-top: 40px;
}
.pieDiv {
  display: flex;
  justify-content: end;
  background-color: #e7e7e6;
  width: 100%;
  text-align: center;
}
.boton-transparente {
  position: absolute;
  top: 50%;
  left: 25%;
  width: 50%;
  padding: 8px;
  color: #fff;
  border: 2px solid #fff;
  text-align: center;
  font-weight: bold;
  outline: none;
  text-decoration: none;
  transition: background-color 0.2s ease-out, color 0.2s ease-out;
  font-size: 26px;
}
.boton-transparente:hover,
.boton-transparente:active {
  background-color: #fff;
  color: #000;
  transition: background-color 0.3s ease-in, color 0.3s ease-in;
}
/* css formulari */

.form {
  width: 340px;
  height: 440px;
  background: #e6e6e6;
  border-radius: 8px;
  box-shadow: 0 0 40px -10px #000;
  margin: calc(50vh - 220px) auto;
  padding: 20px 30px;
  max-width: calc(100vw - 40px);
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  position: relative;
}
.h2form {
  margin: 10px 0;
  padding-bottom: 10px;
  width: 180px;
  color: #78788c;
  border-bottom: 3px solid #78788c;
}
.inputform {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: none;
  outline: none;
  resize: none;
  border: 0;
  font-family: "Montserrat", sans-serif;
  transition: all 0.3s;
  border-bottom: 2px solid #bebed2;
}
.inputform:focus {
  border-bottom: 2px solid #78788c;
}
.pform:before {
  content: attr(type);
  display: block;
  margin: 28px 0 0;
  font-size: 14px;
  color: #5a5a5a;
}

.buttonform {
  float: right;
  padding: 8px 12px;
  margin: 8px 0 0;
  font-family: "Montserrat", sans-serif;
  border: 2px solid #78788c;
  background: 0;
  color: #5a5a6e;
  cursor: pointer;
  transition: all 0.3s;
}
.buttonform:hover {
  background: #78788c;
  color: #fff;
}
.divform {
  content: "Hi";
  position: absolute;
  bottom: -15px;
  right: -20px;
  background: #50505a;
  color: #fff;
  width: 320px;
  padding: 16px 4px 16px 0;
  border-radius: 6px;
  font-size: 13px;
  box-shadow: 10px 10px 40px -14px #000;
}
.spanform {
  margin: 0 5px 0 15px;
}
.titel {
  text-align: center;
  font-size: 50px;
  font-family: sans-serif;
  color: rgb(194, 46, 24);
  /* color:rgb(105, 48, 15); */
}
.legaltitel {
  font-size: 30px;
  font-family: sans-serif;
  color: rgb(194, 46, 24);
  /* color:rgb(105, 48, 15); */
}
.txtlegal {
  font-weight: 400;
  font-size: 18px;
  font-family: sans-serif;
  color: rgb(112, 103, 101);
  line-height: 30px;
}
